<template>
  <header>
    <NavigationBar @login="authorize" @logout="clearToken"/>
  </header>
  <main>
    <div v-if="!product" class="product-img placeholder"></div>
    <img v-else :src="product.image[0].url" :alt="product.image[0].alternativeText" class="product-img">
    <div class="product-details" v-if="product">
      <h4 class="product-type">{{ product.type.name }}</h4>
      <h1>{{ product.title }}</h1>
      <hr />
      <h2>Rp.{{ product.price.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ".") }},00</h2>
      <h4>Publisher: {{ product.publisher.name }}</h4>
      <p>{{ product.description }}</p>
      <button @click.prevent="purchase">Beli</button>
    </div>
  </main>
  <footer>
    <Footer />
  </footer>
  <div class="error-popup" v-if="error_popup">Please login first.</div>
</template>

<script>
import axios from "axios";

import NavigationBar from "../../components/NavigationBar.vue";
import Footer from "../../components/Footer.vue";

export default {
  name: "App",
  components: {
    NavigationBar,
    Footer,
  },
  data() {
    return {
      token: "",
      local_storage: null,
      error_popup: false,
      id: new URL(document.location).searchParams.get("id"),
      product: null,
      apiURL: "https://tabletopid-cms.herokuapp.com",
    };
  },
  mounted() {
    axios
      .get(`${this.apiURL}/products/${this.id}`)
      .then((response) => {
        this.product = response.data;
      })
      .catch(() => (this.product = {}));
  },
  watch: {
    product(newProduct) {
      document.title = `${newProduct.title} - TableTopID`;
    },
    local_storage() {
      const token = localStorage.getItem("token");
      this.token = token;
    },
  },
  methods: {
    purchase() {
      if (!this.token) {
        this.error_popup = true;
        setTimeout(() => (this.error_popup = false), 3000);
        return;
      }
      axios.put(
        `${this.apiURL}/products/${this.id}`,
        {
          sales: ++this.product.sales,
        },
        {
          headers: {
            Authorization: `Bearer ${this.token}`,
          },
        }
      );
    },
    authorize() {
      this.token = localStorage.getItem("token");
    },
    clearToken() {
      this.token = "";
    },
  },
};
</script>

